import { Component, OnInit, OnDestroy,Input } from '@angular/core';
import { Subscription } from 'rxjs';
import { first } from 'rxjs/operators';

import { Observable } from 'rxjs';
import { NgModel } from '@angular/forms';
import { MoviesService } from '../services';
import { ActivatedRoute, Params } from '@angular/router';
import { MovieStoredService } from '../services';
import { User } from '../user';
import { UserService, AuthenticationService } from '../services';
import { Router } from '@angular/router';



@Component({ templateUrl: 'home.component.html',
styleUrls: ['./home.component.css']
})

export class HomeComponent implements OnInit, OnDestroy {
    currentUser: User;
    currentUserSubscription: Subscription;
    users: User[] = [];
    moviesList = [];
    listName = 'popular';
    imgBaseUrl: string;
  posterUrl: string;
  @Input() movie;
  MovieData={ count: 60, data: [] };
  upcomingmovies;
  config: any;
  collection = { count: 60, data: [] };
 
    constructor(private router: Router, private authenticationService: AuthenticationService,private userService: UserService,
      private storage: MovieStoredService, private movies: MoviesService,  private route: ActivatedRoute    ) {
      
        movies.searchFilterEmited$.subscribe(
            filter => {
                this.search(filter);
                this.listName = 'Search results of: ' + filter;
            });

        this.currentUserSubscription = this.authenticationService.currentUser.subscribe(user => {
            this.currentUser = user;
           
        });

        this.storage.getUpComingMovies()
    .subscribe(
      (response) => {
        
        this.upcomingmovies = response['results'];
      
      });

      
    

    }

    ngOnInit() {
        
        this.route.params
        .subscribe((params: Params) => {
            if (params.category === undefined) {
              this.getList('popular');
            } else {
            this.getList(params.category);
            }
          }
        );
        this.loadAllUsers();
    }

    ngOnDestroy() {
        this.currentUserSubscription.unsubscribe();
    }

  
    

    private loadAllUsers() {
        this.userService.getAll().pipe(first()).subscribe(users => {
            this.users = users;
        });
    }

    
    getList(category) {
        this.storage.getList(category)
        .subscribe(
          (response) => {
            this.moviesList = response['results'];
            this.sortByPopularity(this.moviesList);
            debugger
            this.listName = category[0].toUpperCase() + category.slice(1)
            .replace(/_/g, ' ');
            if(this.moviesList.length > 0)
            {
            for(let i=0; i< this.moviesList.length;i++)
            {
              this.imgBaseUrl = this.storage.getImageBaseUrl()
              this.posterUrl = this.imgBaseUrl + 'w154' + this.moviesList[i].poster_path;
              this.MovieData.data.push({'imgpath':this.imgBaseUrl + 'w154' + this.moviesList[i].poster_path,'title':this.moviesList[i].title,'ReleasDate':this.moviesList[i].release_date,'id':this.moviesList[i].id});

            }

            this.config = {
              itemsPerPage: 7,
              currentPage: 1   
            };

          }
         
          }
        );
      }
    
      search(query) {
        this.storage.search(query)
        .subscribe(
          (response) => {
            this.moviesList = response['results'];
            this.sortByPopularity(this.moviesList);
          }
        );
      }
    
      sortByPopularity(list) {
        this.movies.sortByPopularity(list);
        }
    
      sortList() {
        this.movies.sortList(this.moviesList);
      }

      GotoPgae(values:string)
      {
        debugger
        this.storage.setOption(values);

        this.router.navigateByUrl('/MovieDetails');


      }

      pageChanged(event){
        this.config.currentPage = event;
      }
    
}