import { Component, OnInit } from '@angular/core';
import { MovieStoredService } from '../services';
import {HttpClient} from '@angular/common/http';

import { Router } from '@angular/router';

@Component({
  selector: 'app-movie-details',
  templateUrl: './movie-details.component.html',
  styleUrls: ['./movie-details.component.css']
})
export class MovieDetailsComponent implements OnInit {
  list;
  img:string;
  movies;
  
  imgBaseUrl: string;
  posterUrl: string;
  reviews;
  recomandation;
  movivideos;
  id;trandng;
  constructor(private http: HttpClient,  private storage: MovieStoredService,private router: Router,
  ) { 
    this.trending();
  }

  

  trending()
    {
      this.storage.Gettranding().subscribe((response) => {        
        this.trandng = response['results'];
        debugger
      });
    }

  

  ngOnInit() {
    
    this.id = this.storage.getOption();
    if(this.id.toString.length == 0)
    {
      this.router.navigateByUrl('/home');

    }
   this.GetAllData();

    
  }

GetAllData()
{
  this.storage.getreview(this.id).subscribe((response) => {        
    this.reviews = response['results'];
    this.getVideosOfMovies();
  });

  

this.storage.getList(this.id).subscribe((response) => {
    this.movies = response;  
    this.img="";
    this.img   = 'https://image.tmdb.org/t/p/w1920_and_h800_multi_faces' + this.movies.poster_path;
    this.GetRecommandation();
    if (this.movies==undefined) {
      this.posterUrl = 'http://via.placeholder.com/154x218?text=Not+avaliable';
    } else {
      this.imgBaseUrl = this.storage.getImageBaseUrl()
      this.posterUrl = this.imgBaseUrl + 'w154' + this.movies.poster_path;
    }
   }
);
}

GetRecommandation()
{
  this.storage.Getrecommendations(this.id).subscribe((response) => {        
    this.recomandation = response['results'];
  });
}

  getVideosOfMovies()
  {
    

    this.storage.GetMoviesVideos(this.id)
    .subscribe( 
      (response) => {
        
        
        let data1 = response['results'];
        if(data1.length > 0)
        {this.movivideos =[];
          this.movivideos = data1.find(tree => tree.type === "Trailer");
          
          document.getElementById("myIframe").setAttribute("src", "https://www.youtube-nocookie.com/embed/"+ this.movivideos.key);        
}

      });
      
  }


  GotoPgae(values:string)
      {
        debugger
        this.reviews =[];
        this.movivideos=[];
        this.movies=[];
        this.recomandation=[];

        this.id= values;
        this.GetAllData();


      }

       myFunction(id:string) {
        var x = document.getElementById(id);
        if (x.className.indexOf("w3-show") == -1) {
          x.className += " w3-show";
        } else { 
          x.className = x.className.replace(" w3-show", "");
        }
      }

}