export default "/*\r\nFade content bs-carousel with hero headers\r\nCode snippet by maridlcrmn (Follow me on Twitter @maridlcrmn) for Bootsnipp.com\r\nImage credits: unsplash.com\r\n*/\r\n\r\n/********************************/\r\n\r\n/*       Fade Bs-carousel       */\r\n\r\n/********************************/\r\n\r\n.fade-carousel {\r\n    position: relative;\r\n    height: 100vh;\r\n}\r\n\r\n.fade-carousel .carousel-inner .item {\r\n    height: 100vh;\r\n}\r\n\r\n.fade-carousel .carousel-indicators > li {\r\n    margin: 0 2px;\r\n    background-color: #f39c12;\r\n    border-color: #f39c12;\r\n    opacity: .7;\r\n}\r\n\r\n.fade-carousel .carousel-indicators > li.active {\r\n  width: 10px;\r\n  height: 10px;\r\n  opacity: 1;\r\n}\r\n\r\n/********************************/\r\n\r\n/*          Hero Headers        */\r\n\r\n/********************************/\r\n\r\n.hero {\r\n    position: absolute;\r\n    top: 50%;\r\n    left: 50%;\r\n    z-index: 3;\r\n    color: #fff;\r\n    text-align: center;\r\n    text-transform: uppercase;\r\n    text-shadow: 1px 1px 0 rgba(0,0,0,.75);\r\n      transform: translate3d(-50%,-50%,0);\r\n}\r\n\r\n.hero h1 {\r\n    font-size: 6em;    \r\n    font-weight: bold;\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\n.fade-carousel .carousel-inner .item .hero {\r\n    opacity: 0; \r\n    transition: 2s all ease-in-out .1s; \r\n}\r\n\r\n.fade-carousel .carousel-inner .item.active .hero {\r\n    opacity: 1; \r\n    transition: 2s all ease-in-out .1s;    \r\n}\r\n\r\n/********************************/\r\n\r\n/*            Overlay           */\r\n\r\n/********************************/\r\n\r\n.overlay {\r\n    position: absolute;\r\n    width: 100%;\r\n    height: 100%;\r\n    z-index: 2;\r\n    background-color: #080d15;\r\n    opacity: .7;\r\n}\r\n\r\n/********************************/\r\n\r\n/*          Custom Buttons      */\r\n\r\n/********************************/\r\n\r\n.btn.btn-lg {padding: 10px 40px;}\r\n\r\n.btn.btn-hero,\r\n.btn.btn-hero:hover,\r\n.btn.btn-hero:focus {\r\n    color: #f5f5f5;\r\n    background-color: #1abc9c;\r\n    border-color: #1abc9c;\r\n    outline: none;\r\n    margin: 20px auto;\r\n}\r\n\r\n/********************************/\r\n\r\n/*       Slides backgrounds     */\r\n\r\n/********************************/\r\n\r\n.fade-carousel .slides .slide-1, \r\n.fade-carousel .slides .slide-2,\r\n.fade-carousel .slides .slide-3 {\r\n  height: 100vh;\r\n  background-size: cover;\r\n  background-position: center center;\r\n  background-repeat: no-repeat;\r\n}\r\n\r\n.fade-carousel .slides .slide-1 {\r\n  background-image: url(https://images8.alphacoders.com/723/thumb-1920-723808.jpg); \r\n}\r\n\r\n.fade-carousel .slides .slide-2 {\r\n  background-image: url(https://c4.wallpaperflare.com/wallpaper/315/1011/750/joker-joker-2019-movie-joaquin-phoenix-movies-hd-wallpaper-preview.jpg);\r\n}\r\n\r\n.fade-carousel .slides .slide-3 {\r\n  background-image: url(https://i.pinimg.com/originals/75/5d/d5/755dd5fe656eeb2ad8dcde7796226396.jpg);\r\n}\r\n\r\n/********************************/\r\n\r\n/*          Media Queries       */\r\n\r\n/********************************/\r\n\r\n@media screen and (min-width: 980px){\r\n    .hero { width: 980px; }    \r\n}\r\n\r\n@media screen and (max-width: 640px){\r\n    .hero h1 { font-size: 4em; }    \r\n}\r\n\r\n.contenthide\r\n{\r\n  display: block;\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n  line-height: 1.8em;\r\n  }\r\n/*# sourceMappingURL=data:application/json;base64, */"